<template>
  <div class="bookrack">
    <van-nav-bar>
      <template #title>
        <div class="bookrack-title">我的书架</div>
      </template>
      <template #right>
        <div class="right-icon" @click="edit">
          <img
            src="../../assets/images/bianji.png"
            alt
            class="auto-img"
            v-if="!isShowDelete"
          />
          <img
            src="../../assets/images/wanbi.png"
            alt
            class="auto-img"
            v-else
          />
        </div>
      </template>
      <template #left>
        <div class="right-icon-big">
          <img src="../../assets/images/xing.png" alt class="auto-img" />
        </div>
      </template>
    </van-nav-bar>

    <div class="workDay">
      <div class="wordDay-into clearfix">
        <div class="libao fl">
          <img src="../../assets/images/libao.png" alt="" class="auto-img" />
        </div>
        <div class="fl">
          <div class="day">每日签到</div>
          <div class="dai">代金券天天领</div>
        </div>
        <div class="qiandao fr" @click="calendar">签 到</div>
        <van-calendar
          first-day-of-week="1"
          v-model="show"
          color="#016831"
          confirm-text="签到"
          @confirm="onConfirm"
        />
      </div>
    </div>
    <div class="bookracks">
      <div class="bookrack-item clearfix">
        <div
          class="bookrack-list fl"
          v-for="(item, index) in bookList"
          :key="index"
        >
          <div class="bookrack-list-img" @click="golook(item,index)">
            <img :src="item.data.cover" alt class="auto-img list-img-h" />
            <div
              class="delete"
              v-show="isShowDelete"
              @click.stop="deletebook(index)"
            >
              <div class="x">x</div>
            </div>
          </div>
          <div class="bookrack-list-text">{{ item.data.name }}</div>
        </div>

        <div class="bookrack-uploader fl">
          <van-uploader />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/bookrack.less";
export default {
  data() {
    return {
      // 显示删除按钮
      isShowDelete: false,
      // 书架列表
      bookList: [],
      // 签到显示
      show: false,
      //阅读进度
      index: 0,
      // 是否签到
      confirm: false,
    };
  },
  created() {
    //获取书架信息
    this.bookList = JSON.parse(localStorage.getItem("Bookrack"));
  },
  methods: {
    // 编辑
    edit() {
      this.isShowDelete = !this.isShowDelete;
    },
    //阅读
    golook(item,index) {
      // 查看阅读进度
      let progress = JSON.parse(localStorage.getItem("Progress"));
        if(progress){
          let progressIndex = progress.findIndex((v) => {
          return v.bookName == item.data.name;
        });
        if (progressIndex != -1) {
          this.index = progress[progressIndex].index;
        }
      }
      this.$router.push({
        name: "Read",
        query: { bookName: item.data.name, sigh: "书架", index: this.index },
      });
      // 第一本书不需要换位置
      if(index==0){
        return;
      }
      //获取书架信息
      let New=this.bookList.splice(index,1);
      this.bookList.unshift(New[0])
      localStorage.setItem("Bookrack", JSON.stringify(this.bookList));
    },
    // 删除
    deletebook(index) {
      this.bookList.splice(index, 1);
      let Bookrack = JSON.parse(localStorage.getItem("Bookrack"));
      Bookrack.splice(index, 1);
      localStorage.setItem("Bookrack", JSON.stringify(Bookrack));
    },
    // 签到
    calendar() {
      if (this.confirm) {
        this.$dialog({ message: "您已签到" });
        return;
      }
      this.show = true;
    },
    //确认签到
    onConfirm() {
      this.$toast({
        message: "签到成功",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
        icon: require("../../assets/images/qiandao2.png"),
      });

      setTimeout(() => {
        this.$toast.clear();
        this.show = false;
        this.confirm = true;
      }, 1000);
    },
  },
};
</script>

<style lang="less" scoped>
</style>